import React from 'react';

class Pokemon extends React.Component {
  render() {
    const {
      nom,
      type,
      image,
      poids,
      taille,
      capaciteSignature,
    } = this.props.pokemon;
    return (
      <div className="container">
        <h3>{nom}</h3>
        <div className="pokemonContent">
          <div className="pokemonDetail">
            <p>Type : {type}</p>
            <p>
              Poids : {poids.valeur} {poids.unite}{' '}
            </p>
            {taille && (
              <p>
                Taille : {taille.valeur} {taille.unite}
              </p>
            )}
            {capaciteSignature && (
              <p>Capacite Signature : {capaciteSignature}</p>
            )}
          </div>
          <div className="pokemonImage">
            <img src={image} height="140px" alt="PokemonImage" />
          </div>
        </div>
      </div>
    );
  }
}

export default Pokemon;
